/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";
import * as React from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

export default function Page() {
  const [plate, setPlate] = React.useState("");
  const [data, setData] = React.useState<any>(null);
  const [err, setErr] = React.useState<string | null>(null);
  const [loading, setLoading] = React.useState(false);

  async function run() {
    if (!plate) return;
    setLoading(true);
    setErr(null);
    try {
      const res = await fetch(
        `/api/plates?plate=${encodeURIComponent(plate)}&country=nl`
      );
      const j = await res.json();
      if (!res.ok) throw new Error(j?.error || "Plate request failed");
      setData(j);
    } catch (e: any) {
      setErr(e.message);
    } finally {
      setLoading(false);
    }
  }

  const rows = data?.data?.array ?? [];

  return (
    <div className="px-4 lg:px-6 space-y-4">
      <Card className="rounded-2xl">
        <CardHeader>
          <CardTitle>Zoek op kenteken</CardTitle>
        </CardHeader>
        <CardContent className="flex gap-2">
          <Input
            placeholder="Bijv. G123AB"
            value={plate}
            onChange={(e) => setPlate(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && run()}
          />
          <Button onClick={run} disabled={!plate || loading}>
            {loading ? "Zoekenâ€¦" : "Zoek"}
          </Button>
          {err && <span className="text-sm text-rose-600">{err}</span>}
        </CardContent>
      </Card>

      <Card className="rounded-2xl">
        <CardHeader>
          <CardTitle>Resultaten</CardTitle>
        </CardHeader>
        <CardContent className="px-0">
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Vehicle ID</TableHead>
                  <TableHead>Naam</TableHead>
                  <TableHead>Land</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {rows.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={3} className="text-muted-foreground">
                      Geen resultaten
                    </TableCell>
                  </TableRow>
                )}
                {rows.map((r: any) => (
                  <TableRow key={r.carId}>
                    <TableCell className="font-medium">{r.carId}</TableCell>
                    <TableCell>{r.carName}</TableCell>
                    <TableCell>{r.firstCountry}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
